import { Directive, Input, OnInit } from '@angular/core';
import { LookupComponent, LookupGridComponent, PickingResult, PickedResult } from '@farris/ui-lookup';
import { RestfulService } from '../service/restful.service';
import { of, Observable } from 'rxjs';
import { tap, switchMap } from 'rxjs/operators';

@Directive({
  selector: '[libHelpBeforeEvent]'
})
export class HelpBeforeEventDirective implements OnInit {

  @Input("libHelpBeforeEvent") helpData: any = {};
  @Input() localHost: any = {};
  @Input() dynamicInput: any = [];
  constructor(private lookup: LookupGridComponent,
    private restfulService: RestfulService) { }

  ngOnInit(): void {
    console.log(this.helpData, this.lookup);
    this.lookup.dictPicking = this.dictPicking;
    this.lookup.dictPicked = (rowData: any): Observable<PickedResult> => {
      this.helpData.paramvalue = rowData[this.helpData.fieldConfig.valueField] + '|' + rowData[this.helpData.fieldConfig.textField];
      const r$ = of({
        closeDialog: true
      });
      return r$;
    };

  }

  dictPicking = (): Observable<PickingResult> => {
    const options: any = {
      headers: {
        'Content-Type': 'application/json'
      },
      responseType: 'text'
    };
    const paramvalues = {};
    this.dynamicInput.forEach(currentInput => {
      paramvalues[currentInput.paramcode] = currentInput.paramvalue;
    });
    const params = {
      helpId: this.helpData.fieldConfig.helpId,
      filterInfo: this.helpData.fieldConfig.filterInfo,
      parameters: JSON.stringify(paramvalues)
    }
    const getHelpDataUrl = this.localHost + '/api/runtime/bcc/v1.0/qdpsearch/getHelpData';

    const r$ = of({
      showDialog: true
    });


    return this.restfulService.post(getHelpDataUrl, params, {}, options).pipe(
      tap((data) => {
        this.lookup.items = JSON.parse(data);
      }),
      switchMap(() => r$)
    );
  }
}
