import { Injectable} from '@angular/core';

@Injectable()
export class EntityDataService {
  private isFinish: any = false;
  private formularInfo: any = {};
  private stepType: string;

  constructor() {
  }

  setStepType(stepTye: string) {
    this.stepType = stepTye;
  }

  getStepType() {
    return this.stepType;
  }

  setFormularState(isFinish: any, formularInfo: any){
      this.isFinish = isFinish;
      this.formularInfo = formularInfo;
  }

  getFormularState() {
    return {isFinish: this.isFinish , formularInfo: this.formularInfo};
}
}
