  labelBasicInfo: 'åºæ¬ä¿¡æ¯';
  labelFormularName: 'å¬å¼åç§°';
  labelIsGroup: 'æ¯å¦åç»';
  labelParamInfo: 'åæ°ä¿¡æ¯';
